import axios from 'axios'
import React, { useState, useEffect } from 'react'
import { Link, redirect, useLocation, useNavigate, useParams } from 'react-router-dom'
import { Form, Button } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import Message from '../components/Message.js'
import Loader from '../components/Loader.js'
import FormContainer from '../components/FormContainer.js'
import { listProductDetails, updateProduct } from '../actions/productActions.js'
import { PRODUCT_UPDATE_RESET } from '../constants/productConstants.js'


const ProductEditScreen = () => {

   const { id } = useParams()
   const productId = id


   const [name, setName] = useState('')
   const [price, setPrice] = useState(0)
   const [image, setImage] = useState('')
   const [brand, setBrand] = useState('')
   const [category, setCategory] = useState('')
   const [countInStock, setCountInStock] = useState('')
   const [description, setDescription] = useState('')
   const [uploading, setUploading] = useState(false)



   const dispatch = useDispatch()
   let navigateLog = useNavigate()


   const productDetails = useSelector(state => state.productDetails)
   const { loading, error, product } = productDetails

   const productUpdate = useSelector(state => state.productUpdate)
   const { loading: loadingUpdate, error: errorUpdate, success: successUpdate } = productUpdate

   useEffect(() => {
      if (successUpdate) {
         dispatch({ type: PRODUCT_UPDATE_RESET })
         navigateLog('/admin/productlist')
      } else {
         if (!product.name || product._id !== productId) {
            dispatch(listProductDetails(productId))
         } else {
            setName(product.name)
            setPrice(product.price)
            setBrand(product.brand)
            setImage(product.image)
            setCategory(product.category)
            setCountInStock(product.countInStock)
            setDescription(product.description)
         }
      }

   }, [dispatch, product, productId, successUpdate, navigateLog])



   const submitHandler = (e) => {
      e.preventDefault()
      dispatch(updateProduct({
         _id: productId,
         name,
         price,
         image,
         brand,
         category,
         description,
         countInStock
      }))
   }

   const uploadFileHandler = async (e) => {
      const file = e.target.files[0]
      const formData = new FormData()
      formData.append('image', file)
      setUploading(true)

      try {
         const config = {
            headers: {
               'Content-Type': 'multipart/form-data'
            }
         }

         const { data } = await axios.post('/api/upload', formData, config)

         setImage(data)
         setUploading(false)

      } catch (error) {
         console.log(error)
         setUploading(false)
      }
   }
   return (
      <>
         <Link to='/admin/productlist' className='btn btn-light my-3'>
            Go Back
         </Link>
         <FormContainer>
            <h1>Edit Product</h1>
            {loadingUpdate && <Loader />}
            {errorUpdate && <Message variant='danger'> {errorUpdate} </Message>}
            {loading ? (<Loader />) : error ? (<Message variant='danger'> {error} </Message>)
               : (
                  <Form onSubmit={submitHandler}>

                     <Form.Group controlId='name'>
                        <Form.Label>Name</Form.Label>
                        <Form.Control type='name' placeholder='Enter name' value={name} onChange={(e) => setName(e.target.value)}></Form.Control>
                     </Form.Group>

                     <Form.Group controlId='price'>
                        <Form.Label>Price</Form.Label>
                        <Form.Control type='number' placeholder='Enter price' value={price} onChange={(e) => setPrice(e.target.value)}></Form.Control>
                     </Form.Group>

                     <Form.Group controlId='image'>
                        <Form.Label>Image</Form.Label>
                        <Form.Control type='text' placeholder='Enter image url' value={image} onChange={(e) => setImage(e.target.value)}></Form.Control>
                        <Form.Control type='file' controlId='image-file' label='Choose File' custom onChange={uploadFileHandler}></Form.Control>
                        {uploading && <Loader />}
                     </Form.Group>


                     <Form.Group controlId='brand'>
                        <Form.Label>Brand</Form.Label>
                        <Form.Control type='text' placeholder='Enter brand name' value={brand} onChange={(e) => setBrand(e.target.value)}></Form.Control>
                     </Form.Group>

                     <Form.Group controlId='countInStock'>
                        <Form.Label>CountInStock</Form.Label>
                        <Form.Control type='number' placeholder='Enter countInStock' value={countInStock} onChange={(e) => setCountInStock(e.target.value)}></Form.Control>
                     </Form.Group>

                     <Form.Group controlId='category'>
                        <Form.Label>Category</Form.Label>
                        <Form.Control type='text' placeholder='Enter category' value={category} onChange={(e) => setCategory(e.target.value)}></Form.Control>
                     </Form.Group>

                     <Form.Group controlId='description'>
                        <Form.Label>Description</Form.Label>
                        <Form.Control type='text' placeholder='Enter description' value={description} onChange={(e) => setDescription(e.target.value)}></Form.Control>
                     </Form.Group>

                     <Button className='mt-2' type='submit' variant='primary'>Update</Button>
                  </Form>
               )}
         </FormContainer >
      </>

   )
}

export default ProductEditScreen